// @mui
import { Box, Card, Container, Typography } from '@mui/material';
// routes
import { PATH_DASHBOARD } from '../../routes/paths';
// hooks
import useSettings from '../../hooks/useSettings';
// components
import Page from '../../components/Page';
import Markdown from '../../components/Markdown';
import HeaderBreadcrumbs from '../../components/HeaderBreadcrumbs';
import { HOST_API, } from '../../config';
import Network from './network';
// sections
// hooks
import useAuth from '../../hooks/useAuth';
// ----------------------------------------------------------------------
export default function DocsAirtime() {
  const { themeStretch } = useSettings();

  const {setting, user} = useAuth();

  return (
    <Page title="Docs">
      <Container maxWidth={themeStretch ? false : 'lg'}>
        <HeaderBreadcrumbs
          heading="Airtime Subscription API"
          links={[
            { name: 'Dashboard', href: PATH_DASHBOARD.root },
            { name: 'Home' },
          ]}
        />

       
          <Card>

            <Box sx={{ p: { xs: 3, md: 5 } }}>
              <Typography  sx={{ mb: 5 }}>
              This section contains the recommended flow for integrating  Airtime Subscription services on the {setting?.general?.app_name} RESTful API.
                           </Typography>

        
                <Markdown children={`<hr> <h3>Available Endpoints</h3><br/> <p>To integrate the ${setting?.general?.app_name} Airtime Subscription Payment RESTful API, the endpoints below applies:</p>
                <p><br></p><p>
                <ol> <li>network</li> <li>phone</li> <li>plan_type</li> <li>amount</li> <li>bypass (boolean)</li> <li>request-id (unique)</li> </ol>
                
                `} />
           <Network />
           <Markdown children={`<hr> <h3>PURCHASE PRODUCT</h3><br/> <p>Using a POST method,  Airtime purchase can with the endpoint below:</p>
                <p><br></p><p>
                <hr> <h3>Enpoint URL</h3><br/> <blockquote>${HOST_API}/api/topup/</blockquote> <hr> <h3>Code Sample</h3><br/>
                <pre class="ql-syntax" spellcheck="false">
                $paypload = array(
                    'network' => 1,
                     'phone' => ${user?.phone},
                     'plan_type' => 'VTU',
                     'bypass' => false,
                     'amount' => 100,
                     'request-id' => Airtime_12345678900);
                     $ch = curl_init();
                     curl_setopt($ch, CURLOPT_URL, '${HOST_API}/api/topup');
                     curl_setopt($ch, CURLOPT_POST, 1);
                     curl_setopt($ch, CURLOPT_POSTFIELDS, json_encode($paypload));
                     curl_setopt($ch, CURLOPT_RETURNTRANSFER, true);
                     $headers = [
                          "Authorization: Token ${user?.apikey}",
                         'Content-Type: application/json'
                     ];
                     curl_setopt($ch, CURLOPT_HTTPHEADER, $headers);
                     $response = curl_exec($ch);
                     curl_close($ch);
                </pre><br>
                <hr> <h3>Expected Response</h3><br/>
                <pre class="ql-syntax" spellcheck="false">
                {
                    "network": "MTN",
                    "request-id": "Airtime_1234567890",
                    "amount": 100,
                    "discount": 97,
                    "status": "success",
                    "message": "successfully purchase MTN VTU to 07013397088 , ₦100",
                    "phone_number": "07013397088",
                    "oldbal": "110225",
                    "newbal": 110128,
                    "system": "API",
                    "plan_type": "VTU",
                    "wallet_vending": "wallet"
                }
                </pre>
                
                `} />
            </Box>
          </Card>
      </Container>
    </Page>
  );
}
